<template>
    <v-container>
        <v-btn color="orange" @click="saveDiary"><p>일기쓰기</p></v-btn>
        <v-textarea v-model="diaryText" label="쓰는곳" class = "mt-6"></v-textarea>

    </v-container>
    <v-divider class="ma-6" :thickness="3"></v-divider>
    <v-container>
        <v-btn class="mb-6" color="orange" @click="clearDiary"><p>일기초기화</p></v-btn>
        <v-row>
          <v-col class="display-flex" cols="3" v-for="(n,index) in ent" :key="n.date">
          <div>
            <div class="diary-container">
              <div class="diary-text">{{ n.date.slice(4,14) }} {{index+1}}번째 일기</div>
              <div class="delete-icon" @click="deleteDiary(n)">
                <v-icon class="mdi mdi-delete-circle"></v-icon>
              </div>
            </div>
          </div>
            <v-textarea v-model="n.diary"></v-textarea>
          </v-col>
        </v-row>

    </v-container>
</template>

<script>
export default {
  mounted () {
    this.pushDiary()
    if (localStorage.getItem('count') == null) {
      localStorage.setItem('count', 1)
    }
    
  },
  data () {
    return {
      diaryText: '',
      count: 1,
      ent: []
    }
  },
  methods: {
    getCurrentDate () {
      const today = new Date()
      const year = today.getFullYear()
      const month = String(today.getMonth() + 1).padStart(2, '0')
      const day = String(today.getDate()).padStart(2, '0')
      return `${year}-${month}-${day} `
    },
    pushDiary () {
      const entries = []
      for (let i = 0; i < localStorage.length; i++) {
        const key = localStorage.key(i)
        const value = localStorage.getItem(key)

        if (key.slice(0, 4) === 'date') {
          entries.push({ date: key, diary: value })
        }
      }
      this.ent = entries

      const sortedData = this.ent.sort((a, b) => {
        let aKey = Object.values(a)[0] // 첫 번째 키를 가져옴
        let bKey = Object.values(b)[0]
        aKey = aKey.slice(4)
        bKey = bKey.slice(4)
        const [aDate, aNumber] = aKey.split(' ')
        const [bDate, bNumber] = bKey.split(' ')

        if (aDate !== bDate) {
          return new Date(aDate) - new Date(bDate)
        } else {
          return parseInt(aNumber) - parseInt(bNumber)
        }
      })

      this.ent = sortedData
      
    },

    // 일기 저장 함수
    saveDiary () {
      this.count = parseInt(localStorage.getItem('count'))
      const date = 'date' + this.getCurrentDate() + this.count
      this.count += 1
      localStorage.setItem('count', this.count)
      const diaryContent = this.diaryText
      if (this.diaryText === '') {
        return
      }
      this.diaryText = ''

      // 로컬 스토리지에 날짜와 일기 내용 저장
      localStorage.setItem(date, diaryContent)

      this.pushDiary()

      alert('일기가 저장되었습니다!')
    },
    clearDiary () {
      var confirmflag = confirm("정말 초기화 하시겠습니까?");

      if(!confirmflag){

        return 

      }


      let keys = [];
      for (let i = 0; i < localStorage.length; i++) {
          keys.push(localStorage.key(i));
      }

      // Iterate over the array and remove items
      for (let i = 0; i < keys.length; i++) {
          const key = keys[i];
          if (key.slice(0, 4) === 'date') {
              localStorage.removeItem(key);
          }
      }

      this.ent = []
      // alert(localStorage.length);
      localStorage.setItem('count', 1)
    },
    deleteDiary (diary) {
      localStorage.removeItem(Object.values(diary)[0])
      this.pushDiary()
      alert('일기가 삭제되었습니다!')
    }

  }

}
</script>

<style>
.col-example {
  background-color: lightblue;
}
.diary-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.delete-icon {
  cursor: pointer;
}
</style>
