<template >
    <v-main class="pa-0">
        <v-card class="mx-auto flex-column align-center justify-center" elevation="4" style="width: 70%; " >
            <p style="font-size:  42px;text-align:center; margin: 20px;">가계부</p>
            <v-row class=" ml-1" style="width: 100%">
                <v-col cols="5"  >
                    <v-text-field v-model="money" type="number" label="금액을 입력하세요"></v-text-field>
                    
                </v-col >
                <v-col cols="5">
                    <v-text-field  v-model="text" label="쓰고 싶은 말을 입력하세요"></v-text-field>
                    
                </v-col>
                <v-col cols="2"  >
                    <v-btn @click="input" class="bg-light-green-darken-1" width="100%" height="75%">
                        입력
                    </v-btn>
                    
                </v-col>
                
            </v-row >
            <v-row style="width: 100%"> 
                <v-col cols = "4">
                    <v-card class="ma-5 mr-0" style="text-align: center; border: 10px solid #863A6F;">
                        <v-card-title>
                            <h2 class="ma-5">소득</h2>
                        </v-card-title>
                        <v-divider style="border-top: 10px solid #863A6F; opacity: 1;">
                        </v-divider>
                        <v-card-text>
                            <p class="ma-10" :v-bind="add" style="font-size: 36px;">{{add}}원</p>
                        </v-card-text>
                    </v-card>
                </v-col>
                <v-col cols = "4">
                    <v-card class="ma-5 mr-0" style="text-align: center; border: 10px solid #975C8D;">
                        <v-card-title>
                            <h2 class="ma-5">지출</h2>
                        </v-card-title>
                        <v-divider style="border-top: 10px solid #975C8D; opacity: 1;">
                        </v-divider>
                        <v-card-text>
                            <p class="ma-10" :v-bind="minus" style="font-size: 36px;">{{minus}}원</p>
                        </v-card-text>
                    </v-card>
                </v-col>
                <v-col cols = "4">
                    <v-card class="ma-5 mr-0" style="text-align: center; border: 10px solid #D989B5;">
                        <v-card-title>
                            <h2 class="ma-5">총합</h2>
                        </v-card-title>
                        <v-divider style="border-top: 10px solid #D989B5; opacity: 1;">
                        </v-divider>
                        <v-card-text>
                            <p class="ma-10" :v-bind="sum" style="font-size: 36px;">{{sum}}원</p>
                        </v-card-text>
                    </v-card>
                </v-col>
            </v-row>
            <v-table class="ma-5">
            <thead >
                <tr>
                <th class="text-center" width ="30%">
                    기록시간
                </th>
                <th class="text-center" width ="30%">
                    금액
                </th>
                <th class="text-center">
                    기록
                </th>
                <th width ="10%">
                    삭제
                </th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in household" :key="item.date">
                    <td class="text-center">{{item.time}}</td>
                    <td class="text-center">{{item.money}}</td>
                    <td class="text-center">{{item.text}}
                        
                    </td>
                    <td>
                        <v-btn @click="deleteHousehold(item)" class="bg-red-darken-1"  style="width: 35px;height: 35px; ">
                            삭제
                        </v-btn>
                    </td>
                </tr>
            </tbody>
            </v-table>

            
        </v-card>
        
    </v-main>
</template>

<script>


export default {
    mounted () {
    
    this.pushHousehold();
    
    },
    data () {
      return {
        time:'',
        money : 0,
        text : '',
        household: [
          
        ],
        add:0,
        minus:0,
        sum:0,

      }
    },
    methods:{
        getCurrentDate () {
        const today = new Date();
        const year = today.getFullYear();
        const month = String(today.getMonth() + 1).padStart(2, '0');
        const day = String(today.getDate()).padStart(2, '0');
        const hours = String(today.getHours()).padStart(2, '0');
        const minutes = String(today.getMinutes()).padStart(2, '0');
        const seconds = String(today.getSeconds()).padStart(2, '0');

        return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
        },
        input(){
            if(this.money==='0'||this.money===''||this.text===''){
                return;
            }


            const num = 100; 
            let nowTime = this.getCurrentDate()
            let key = 'household'+nowTime;
            let value = {
                time:nowTime,
                money:this.money,
                text:this.text
            }
            localStorage.setItem(key,JSON.stringify(value))
            value = JSON.parse(localStorage.getItem(key))
            this.pushHousehold()
            
        }
        ,pushHousehold(){
            this.household = []
            this.add = 0;
            let keys = [];
            for (let i = 0; i < localStorage.length; i++) {
            keys.push(localStorage.key(i));
            }
            let a = 0
            // Iterate over the array and remove items
            for (let i = 0; i < keys.length; i++) {
            const key = keys[i];
            if (key.slice(0, 9) === 'household') {
                let value = JSON.parse(localStorage.getItem(key))
                this.household.push({
                    time:value.time,
                    money:value.money,
                    text:value.text
                })
                
            }
            }
            
            this.household.sort((a, b) => b.time.localeCompare(a.time));
            this.calcNumber();
        },
        clearHousehold () {


        let keys = [];
        for (let i = 0; i < localStorage.length; i++) {
            keys.push(localStorage.key(i));
        }
        let a = 0
        // Iterate over the array and remove items
        for (let i = 0; i < keys.length; i++) {
            const key = keys[i];
            if (key.slice(0, 9) === 'household') {
                a++
                localStorage.removeItem(key);
            }
        }
        
        this.household= []

        },
        deleteHousehold (household) {
        localStorage.removeItem('household'+household.time)
        this.pushHousehold()
        alert('가계부가 삭제되었습니다!')
        },
        calcNumber(){
            
            this.sum = 0
            this.add = 0
            this.minus = 0
            this.household.forEach(item => {
                let money = Number(item.money)
                this.sum+=money
                if(money>=0){
                    this.add+=money
                }
                else{
                    this.minus+=money
                }
            });
        }   

    }
}
</script>

